import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { map, Observable, switchMap } from "rxjs";
import { environment } from "../../../environments/environment";
import { RaceService } from "../../race/service/race.service";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { ChartColorUpdateService } from "./chart-color-update.service";
import { ChartColor, deserializeChartColorDTOs } from "../model/chart-color";

@Injectable({
  providedIn: 'root'
})
export class ChartColorDataService {

  private readonly path: string = '/chart-color-management';
  private readonly httpClient = inject(HttpClient);

  private readonly chartColorUpdateService = inject(ChartColorUpdateService);
  private readonly raceService = inject(RaceService);

  readonly chartColors: WritableSignal<ChartColor[]> = signal([]);
  readonly chartColorsByRaceNumber: WritableSignal<Map<number,string>> = signal(new Map<number, string>());

  constructor() {

    // Handle race changes - reset chartColors when active race changes
    toObservable(this.raceService.activeRace).pipe(
      switchMap(race => race ? this.getChartColors(race.id, []) : []),
      takeUntilDestroyed(),
    ).subscribe(chartColors => {
      this.chartColors.set(this.merge(this.chartColors(), chartColors));
      this.chartColorsByRaceNumber.set(new Map(this.chartColors().map(chartColor => [chartColor.raceNumber, chartColor.color])))
    });

    // Handle chartColor updates - merge updated chartColors with existing ones
    toObservable(this.chartColorUpdateService.chartColorUpdate).pipe(
      switchMap(chartColorUpdate => chartColorUpdate ? this.getChartColors(chartColorUpdate.raceId, chartColorUpdate.updatedIds) : []),
      takeUntilDestroyed(),
    ).subscribe(updatedChartColors => {
      this.chartColors.set(this.merge(this.chartColors(), updatedChartColors));
      this.chartColorsByRaceNumber.set(new Map(this.chartColors().map(chartColor => [chartColor.raceNumber, chartColor.color])))
    });
  }

  private merge(currentChartColors: ChartColor[], newChartColors: ChartColor[]): ChartColor[] {
    // Crea una mappa dei chartColor attuali per ID per accesso rapido
    const currentChartColorsMap = new Map(currentChartColors.map(chartColor => [chartColor.id, chartColor]));
    
    // Aggiorna o aggiungi i nuovi chartColor
    newChartColors.forEach(newChartColor => {
      currentChartColorsMap.set(newChartColor.id, newChartColor);
    });
    
    // Converte la mappa in array e ordina per ID
    return Array.from(currentChartColorsMap.values()).sort((a, b) => a.id - b.id);
  }

  private getChartColors(raceId: number, ids: number[]): Observable<ChartColor[]> {
    return this.httpClient
    .post<ChartColor[]>(
        `${environment.baseUrl}${this.path}/chart-colors`,
        {
          raceId: raceId,
          ids: ids
        }
    )
    .pipe(map((result)=> deserializeChartColorDTOs(result)));
  }
}
