import { Injectable, signal, WritableSignal } from '@angular/core';
import { FirestoreService } from "../../shared/service/firestore.service";
import { collection, collectionData } from "@angular/fire/firestore";
import { takeUntilDestroyed } from "@angular/core/rxjs-interop";
import { map, Observable, takeUntil } from "rxjs";
import { DocumentData } from "@angular/fire/compat/firestore";
import { GapChartConfig } from "../model/gap-chart-config";

@Injectable({
  providedIn: 'root'
})
export class GapChartConfigService extends FirestoreService {
  protected collectionPath: string = '/gap-chart-configs';
  protected collectionRef = collection(this.firestore, this.collectionPath);

  readonly gapChartConfig: WritableSignal<GapChartConfig | undefined> = signal(undefined);

  constructor() {
    super();

    this.getAll()
    .pipe(takeUntilDestroyed())
    .subscribe(configs => {
      if (configs.length > 0) {
        this.gapChartConfig.set(configs.at(0));
      }
    });
  }

  updateLapWindow(value: number){
    const config = this.gapChartConfig();
    if (config) {
      config.lapWindow = value;
      this.updateDataWithMessage(config.id, config, 'lap window');
    }
  }

  updateGapWindow(value: number) {
    const config = this.gapChartConfig();
    if (config) {
      config.gapWindowMilliseconds = value;
      this.updateDataWithMessage(config.id, config, 'gap window');
    }
  }

  private getAll(): Observable<GapChartConfig[]> {
    return collectionData(this.collectionRef).pipe(
        takeUntil(this.destroyed),
        map((data: DocumentData[]) => data.map(doc => doc as GapChartConfig).filter(config => !config.deleted))
    );
  }


}
