

export interface ChartColor {
  id: number;
  raceId: number;
  raceNumber: number;
  color: string;
}

export function deserializeChartColorDTO(data: any): ChartColor {
  return {
    id: data.id,
    raceId: data.raceId,
    raceNumber: data.raceNumber,
    color: data.color,
  };
}

export function deserializeChartColorDTOs(data: any[]): ChartColor[] {
  return data.map(item => deserializeChartColorDTO(item));
}
