import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest } from "rxjs";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { LapSimulatorService } from "../../lap-simulator/service/lap-simulator.service";
import { GapChartConfigService } from "./gap-chart-config.service";
import {
  LapSimulatorConfigService
} from "../../lap-simulator/service/lap-simulator-config.service";
import { TeamService } from "../../team/service/team.service";
import { ChartColorDataService } from "../../chart/service/chart-color-data.service";

@Injectable({
  providedIn: 'root'
})
export class GapChartService {

  private readonly lapSimulatorService = inject(LapSimulatorService);
  private readonly lapSimulatorConfigService = inject(LapSimulatorConfigService);
  private readonly gapChartConfigService = inject(GapChartConfigService);
  private readonly gapChartColorDataService = inject(ChartColorDataService);
  private readonly teamService = inject(TeamService);

  private readonly worker: Worker | undefined;

  readonly chartData: WritableSignal<any> = signal(undefined);

  constructor() {
    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./gap-chart.worker', import.meta.url));
      this.worker.onmessage = ({ data }) => {
        this.chartData.set(data.chartData);
      };
    }

    combineLatest({
      normalizedCumulatedTimeLaps: toObservable(this.lapSimulatorService.normalizedCumulatedTimeLaps),
      shadowLaps: toObservable(this.lapSimulatorService.shadowLaps),
      futureLaps: toObservable(this.lapSimulatorService.futureLaps),
      futureLapsWithPitNow: toObservable(this.lapSimulatorService.futureLapsWithPitNow),
      teamNamesByRaceNumber: toObservable(this.teamService.teamNameByRaceNumber),
      gapChartVisibleByRaceNumber: toObservable(this.teamService.gapChartVisibleByRaceNumber),
      shadowChartVisibleByRaceNumber: toObservable(this.teamService.shadowChartVisibleByRaceNumber),
      pitNowChartVisibleByRaceNumber: toObservable(this.teamService.pitNowChartVisibleByRaceNumber),
      lapSimulatorConfig: toObservable(this.lapSimulatorConfigService.lapSimulatorConfig),
      gapChartConfig: toObservable(this.gapChartConfigService.gapChartConfig),
      gapChartColorsByRaceNumber: toObservable(this.gapChartColorDataService.chartColorsByRaceNumber),
    })
    .pipe(takeUntilDestroyed())
    .subscribe((
        {
          normalizedCumulatedTimeLaps,
          shadowLaps,
          futureLaps,
          futureLapsWithPitNow,
          teamNamesByRaceNumber,
          gapChartVisibleByRaceNumber,
          shadowChartVisibleByRaceNumber,
          pitNowChartVisibleByRaceNumber,
          lapSimulatorConfig,
          gapChartConfig,
          gapChartColorsByRaceNumber,
    }) => {
      if (this.worker && gapChartConfig && lapSimulatorConfig && normalizedCumulatedTimeLaps.size > 0) {
        this.worker.postMessage({
          normalizedCumulatedTimeLaps: normalizedCumulatedTimeLaps,
          teamNamesByRaceNumber: teamNamesByRaceNumber,
          gapChartVisibleByRaceNumber: gapChartVisibleByRaceNumber,
          shadowChartVisibleByRaceNumber: shadowChartVisibleByRaceNumber,
          pitNowChartVisibleByRaceNumber: pitNowChartVisibleByRaceNumber,
          shadowLaps: shadowLaps,
          futureLaps: futureLaps,
          futureLapsWithPitNow: futureLapsWithPitNow,
          colorByRaceNumber: gapChartColorsByRaceNumber,
          gapChartConfig: gapChartConfig,
          lapSimulatorConfig: lapSimulatorConfig,
        });
      }
    });
  }
}
