import {
  Component,
  effect,
  inject,
  OnInit,
  signal,
  ViewChild,
  WritableSignal
} from '@angular/core';
import { BaseChartDirective } from "ng2-charts";
import { ChartConfiguration, ChartEvent, ChartType } from "chart.js";
import { GapChartService } from "../../service/gap-chart.service";
import { FormsModule } from "@angular/forms";
import { CardComponent } from '../../../shared/component/card/card.component';
import { ChartLoaderService } from '../../../shared/service/chart-loader.service';
import { TeamService } from '../../../team/service/team.service';
import { LapService } from '../../../lap/service/lap.service';
import { millisecondsToMinuteSecondMillisecondsString } from '../../../util/date.util';
import { LapDataService } from '../../../lap/service/lap-data.service';

@Component({
  selector: 'app-gap-chart',
  imports: [
    BaseChartDirective,
    FormsModule,
    CardComponent
  ],
  templateUrl: './gap-chart.component.html',
  styleUrl: './gap-chart.component.css'
})
export class GapChartComponent implements OnInit {

  private readonly gapChartService = inject(GapChartService);
  private readonly chartLoaderService = inject(ChartLoaderService);
  private readonly teamService = inject(TeamService);
  private readonly lapService = inject(LapService);
  private readonly lapDataService = inject(LapDataService);

  @ViewChild(BaseChartDirective) chart?: BaseChartDirective;

  chartsReady: WritableSignal<boolean> = signal(false);

  constructor() {
    effect(() => {
      const chartData = this.gapChartService.chartData();
      if (chartData && this.chartsReady()) {
        this.chartData = chartData;
      }
    });

    this.onResize();
    window.addEventListener('resize', this.onResize.bind(this));
  }

  async ngOnInit() {
    await this.chartLoaderService.loadCharts();
    this.chartsReady.set(true);
  }

  onResize() {
    this.hideLegend = window.innerWidth <= 700;
  }

  // Dati per chart che non supportano ancora i signal
  public hideLegend = false;
  public chartData: ChartConfiguration['data'] = {
    datasets: [],
  };

  public chartOptions: ChartConfiguration['options'] = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      y: {
        position: 'left',
      }
    },
    plugins: {
      legend: { display: true },
      // NOTE: inserire informazioni utili sul tooltip
      tooltip : {
        callbacks: {
          label: (context) => {
            return this.createTooltipData(context);
          }
        }
      }
    },
    animation: false,
    animations: {
      colors: false,
      x: false
    },
    transitions: {
      active: {
        animation: {
          duration: 0,
        }
      }
    },
  };

  public chartType: ChartType = 'scatter';

  // events
  public chartClicked({ event, active }: {
    event?: ChartEvent;
    active?: object[];
  }): void {
    // console.log(event, active);
  }

  public chartHovered({ event, active}: {
    event?: ChartEvent;
    active?: object[];
  }): void {
    // console.log(event, active);
  }

  private createTooltipData(context: any): string[] {
    const result: string[] = [];

    const dataType = this.extractDataType(context.dataset.label || '');
    if (dataType !== 'CURRENT') {
      result.push(`${dataType}`);
    }

    const raceNumber = this.extractRaceNumber(context.dataset.label || '');
    const teamName = this.getTeamName(raceNumber);
    if (raceNumber) {
      if (teamName) {
        result.push(`${teamName} (#${raceNumber})`);
      } else {
        result.push(`#${raceNumber}`);
      }
    }

    const dataPoint = context.dataset.data[context.dataIndex] as any;
    const lapNumber = dataPoint.x;
    if (lapNumber) {
      result.push(`Lap: ${lapNumber}`);
    }

    const lapTime = this.getLapTime(raceNumber, lapNumber);
    if (lapTime && dataType === 'CURRENT') {
      result.push(`Lap Time: ${lapTime}`);
    }

    const currentAVG = this.getCurrentPace(raceNumber);
    if (currentAVG && dataType === 'CURRENT') {
      result.push(`Current AVG: ${currentAVG}`);
    }

    // Return array of strings for proper line breaks in tooltip
    return result;
  }

  private extractDataType(label: string): string | undefined {
    // Extract data type from different label formats:
    // 15 - nome team (CURRENT - no prefix)
    // FUTURE - 14 - nome team
    // SHADOW - 15 - nome team
    // PIT NOW - 15 - nome team

    const parts = label.split(' - ');

    // Case 1: "15 - nome team" (no prefix, so it's CURRENT)
    if (parts.length >= 2 && !isNaN(Number(parts[0]))) {
      return 'CURRENT';
    }

    // Case 2: "FUTURE - 14 - nome team", "SHADOW - 15 - nome team", "PIT NOW - 15 - nome team"
    // Data type is the first part
    if (parts.length >= 3) {
      const dataType = parts[0].trim();
      if (['FUTURE', 'SHADOW', 'PIT NOW'].includes(dataType)) {
        return dataType;
      }
    }

    return undefined;
  }

  private extractRaceNumber(label: string): number | undefined{
    // Extract race number from different label formats:
    // 15 - nome team
    // FUTURE - 14 - nome team
    // SHADOW - 15 - nome team
    // PIT NOW - 15 - nome team

    const parts = label.split(' - ');

    // Case 1: "15 - nome team" (race number is first part)
    if (parts.length >= 2 && !isNaN(Number(parts[0]))) {
      return Number(parts[0]);
    }

    // Case 2: "FUTURE - 14 - nome team", "SHADOW - 15 - nome team", "PIT NOW - 15 - nome team"
    // Race number is the second part
    if (parts.length >= 3 && !isNaN(Number(parts[1]))) {
      return Number(parts[1]);
    }

    // Fallback: try to find any number in the label
    const numberMatch = label.match(/\d+/);
    if (numberMatch) {
      return Number(numberMatch[0]);
    }

    // If no number found, return 0 as default
    return undefined;
  }

  private getTeamName(raceNumber: number | undefined): string | undefined {
    if (raceNumber === undefined) {
      return undefined;
    }
    return this.teamService.teamNameByRaceNumber().get(raceNumber);
  }

  private getLapTime(raceNumber: number | undefined, lapNumber: number | undefined): string | undefined {
    if (raceNumber === undefined || lapNumber === undefined) {
      return undefined;
    }

    const lap = this.lapDataService.laps().find(lap => lap.raceNumber === raceNumber && lap.lapNumber === lapNumber);
    if (lap) {
      return millisecondsToMinuteSecondMillisecondsString(lap.lapMilliseconds);
    }
    return undefined;
  }

  private getCurrentPace(raceNumber: number | undefined): string | undefined {
    if (raceNumber === undefined) {
      return undefined;
    }

    const value = this.lapService.pacesByRaceNumber().get(raceNumber);
    if (value) {
      return millisecondsToMinuteSecondMillisecondsString(value);
    }
    return undefined;
  }
}
