import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { combineLatest, map, Observable, takeUntil } from "rxjs";
import { RaceService } from "../../race/service/race.service";
import { FirestoreService } from "../../shared/service/firestore.service";
import { collection, collectionData } from "@angular/fire/firestore";
import { takeUntilDestroyed, toObservable } from "@angular/core/rxjs-interop";
import { ChartColorUpdate } from "../model/chart-color-update";

@Injectable({
  providedIn: 'root'
})
export class ChartColorUpdateService extends FirestoreService {
  protected override collectionPath: string = '/chart-color-updates';
  protected override collectionRef = collection(this.firestore, this.collectionPath);

  private readonly raceService = inject(RaceService);

  readonly chartColorUpdate: WritableSignal<ChartColorUpdate | undefined> = signal(undefined)

  constructor() {
    super();
    combineLatest({
      chartColorUpdate: this.getChartColorUpdate()
    }).pipe(takeUntilDestroyed())
    .subscribe(({chartColorUpdate}) => {
      if (chartColorUpdate) {
        const actualChartColorUpdate = this.chartColorUpdate();
        if (!actualChartColorUpdate || actualChartColorUpdate.updatedAt < chartColorUpdate.updatedAt) {
          this.chartColorUpdate.set(chartColorUpdate);
        }
      }
    });
  }

  private getChartColorUpdate(): Observable<ChartColorUpdate | undefined> {
    return combineLatest({
      chartColorUpdates: collectionData(this.collectionRef),
      activeRace: toObservable(this.raceService.activeRace)
    })
    .pipe(
        takeUntil(this.destroyed),
        map(({chartColorUpdates, activeRace}) => {
          const result = chartColorUpdates as ChartColorUpdate[];
          if (activeRace && result.length > 0) {
            return result
            .filter(chartColorUpdate => !chartColorUpdate.deleted && chartColorUpdate.raceId === activeRace.id)
            .at(0);
          }
          return undefined;
        })
    );
  }
}
